:root {
  view-transition-name: none;
}

body {
  width: 100%;
  height: 100%;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: all .5s ease-in-out;
  display: flex;
  position: absolute;
  overflow: hidden;
}

.dark {
  color: #fff;
  background-color: #222;
}

.dark a {
  color: #88f;
}

.dark nav, .dark footer {
  background-color: #111;
}

.dark button {
  color: #fff;
  background: #333;
}

.dark button:hover {
  background: #444;
}

.purple {
  color: #000;
  background-color: #88f;
}

.purple a {
  color: #333;
}

.purple nav, .purple footer {
  background-color: #77e;
}

.purple button {
  color: #fff;
  background: #a0f;
}

.purple button:hover {
  background: #b0c;
}

.green {
  color: #911;
  background-color: #0c0;
}

.green a {
  color: #fff;
}

.green nav, .green footer {
  background-color: #060;
}

.green button {
  color: #fff;
  background: #3a3;
}

.green button:hover {
  background: #080;
}

ul {
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
  display: flex;
}

nav, footer {
  width: 100%;
  view-transition-name: none;
  background-color: #222;
  position: absolute;
}

nav {
  z-index: 1;
  top: 0;
}

#hamburger {
  z-index: 2;
  cursor: pointer;
  display: none;
  position: fixed;
  top: 20px;
  left: 20px;
}

main {
  width: 100%;
  scroll-behavior: smooth;
  view-transition-name: main;
  margin-top: 4.5rem;
  margin-bottom: 1rem;
  overflow-y: auto;
}

input {
  max-width: 100%;
  border: 2px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  transition: all .5s ease-in-out;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  align-self: start;
  padding: 10px 20px;
}

#toggleTheme {
  margin-right: 2rem;
}

img {
  aspect-ratio: 1 / 1;
  object-fit: contain;
  width: 100%;
  max-height: 30vh;
}

.activeImage {
  max-height: 50vh;
}

.spinner {
  width: 100px;
  height: 100px;
  border: 10px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  animation: 2s linear infinite spin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

footer {
  bottom: 0;
}

h1 {
  view-transition-name: h1;
}

::view-transition-old(main) {
  animation: .8s ease-in-out slide-left;
}

::view-transition-new(main) {
  animation: .8s ease-in-out slide-right;
}

::view-transition-group(activeImage), ::view-transition-group(h1) {
  animation-duration: .8s;
}

::view-transition-old(activeImage), ::view-transition-old(h1) {
  opacity: 0;
}

::view-transition-new(activeImage) {
  opacity: 1;
  animation: .5s ease-in-out scaleUp;
}

::view-transition-new(h1) {
  opacity: 1;
  animation: none;
}

@keyframes scaleUp {
  from {
    transform: scale(.5);
  }
}

@keyframes slide-left {
  to {
    opacity: 0;
    transform: translateX(-75%);
  }
}

@keyframes slide-right {
  from {
    transform: translateX(100%);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (width <= 768px) {
  nav {
    display: none;
  }

  nav ul {
    width: 100%;
    height: 0;
    background: inherit;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    transition: all .5s ease-in-out;
    position: absolute;
    top: 0;
    left: 0;
  }

  main {
    margin-top: 10px;
  }

  #hamburger {
    display: block;
  }

  #toggleTheme {
    margin-right: 0;
  }
}

